.header-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header-logo,.header-avatar{
    font-family: 'Luckiest Guy';
    font-size: 3em;
    font-weight: bolder;
    background-color: #FBFAF7;
    padding: 10px 20px;
    border-radius: 20px;
}
.header-avatar{
    font-size: 2em;
    width: 80px;
    height: 80px;
    border-radius: 100px;
    display: grid;
    place-items: center;
}