.dailyTask-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: grid;
    padding-right: 20px;
    overflow-y: auto;
}
.dailyTask-container::-webkit-scrollbar{
    display: none;
}
.dailyTask-header{
    position: relative;
    width: 100%;
    grid-column: 1;
    grid-row: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.dailyTask-title{
    position: relative;
    font-family: 'Luckiest Guy';
    font-size: 2em;
    padding-left: 20px;
    margin-left: 20px;
}
.title-text{
    color: var(--color5);
    position: relative;
    z-index: 9;
}
.dailyTask-title::after{
    content: '';
    position: absolute;
    width: 80%;
    height: 50%;
    top: 50%;
    left: 0;
    background-color: var(--color1);
    z-index: 1;
    box-shadow: -10px 10px 30px 0px rgba(0, 0, 0, 0.212);
}
.dailyTask-list-wrapper{
    position: relative;
    width: 100%;
    grid-column: 1;
    grid-row: 2/ span 10;
    flex-grow: none;
    padding: 10px 20px;
    overflow-y: auto;
}
.dailyTask-list-wrapper::-webkit-scrollbar{
    display: none;
}
.dailyTask-list{
    position: absolute;
    width: 90%;
}
.dailyTask-list-item{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.dailyTask-list-caption{
    position: relative;
    display: flex;
    align-items: flex-end;
}
.dailyTask-list-caption-no{
    position: relative;
    font-size: 2em;
    line-height: 100%;
}
.dailyTask-list-caption-no-text{
    color: var(--color5);
    position: relative;
    z-index: 2;
    font-family: 'Luckiest Guy';
}
.dailyTask-list-caption-no::after{
    content: '';
    position: absolute;
    width: 200%;
    height: 100%;
    top: 20%;
    left: -90%;
    background-color: var(--color1);
    z-index: 1;
    box-shadow: -10px 10px 30px 0px rgba(0, 0, 0, 0.212);
}
.dailyTask-list-caption-title{
    font-family: sans-serif;
    color: var(--color3);
    position: relative;
    margin-left: 20px;
    margin-right: 20px;
}
.dailyTask-list-item-btns{
    display: flex;
    flex-wrap: nowrap;
}
.dailyTask-addTodo-btn{
    color: var(--color5);
    font-size: 2em;
    cursor: pointer;
    padding-right: 20px;
    z-index: 100;
}
.btn{
    margin-left: 10px;
    background: transparent;
    border: none;
    font-size: 1.5em;
    color: var(--color5);
    cursor: pointer;
}
.delete-btn{
    color: var(--color4)
}
.form-wrapper{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%)
}
.form-wrapper-hide{
    display: none;
}
.line-through{
    text-decoration: line-through;
    color: black
}
@media only screen and (max-width: 428px){
    .dailyTask-container{
        width: 100%;
    }
}