.projects-container{
    position: relative;
    display: grid;
    width: 100%;
    height: 100%;
}
.projects-header{
    position: relative;
    grid-column: 1;
    grid-row: 1;
    display: flex;
    justify-content: space-between;
    min-width: fit-content;
    align-items: center;
}
.projects-header-title{
    position: relative;
    font-family: 'Luckiest Guy';
    font-size: 2em;
    padding-left: 20px;
    margin-left: 20px;
}
.projects-header-title-text{
    color: var(--color5);
    position: relative;
    z-index: 2;
}
.projects-header-title::after{
    content: '';
    position: absolute;
    width: 80%;
    height: 50%;
    top: 50%;
    left: 0;
    background-color: var(--color1);
    z-index: 1;
    box-shadow: -10px 10px 30px 0px rgba(0, 0, 0, 0.212);
}
.addproject-btn{
    font-size: 2em;
    color: var(--color5);
    padding-right: 20px;
    cursor: pointer;
    z-index: 100;
}
.projects-items-wrapper{
    grid-column: 1;
    grid-row: 2 / span 10;
}
.project-form-wrapper{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.project-form-wrapper-hide{
    display: none;
}
.projects-items-wrapper{
    position: relative;
}
.projects-items-wrapper-2{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
}
.projects-items-wrapper-2::-webkit-scrollbar{
    display: none;
}
.project-list-item-wrapper{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-height: 100px;
    margin: 2em;
    flex-grow: none;
    padding-top: 50px;
}
.project-list-item-title{
    font-family: sans-serif;
    color: var(--color3);
    font-size: 1em;
    z-index: 2;
}
.project-list-item-btns{
    display: flex;
}
.project-item-btn{
    border: none;
    background: transparent;
    font-size: 1.5em;
    margin-right: 1em;
    color: #FBFAF7;
    z-index: 2;
    cursor: pointer;
}
.delete-btn{
    color: var(--color4);
}
.counter{
    position: absolute;
    font-size: 8em;
    font-family: 'Luckiest Guy';
    color: rgba(0,0,0,.2);
    right: 0;
    bottom: -40%;
}
.project-form-wrapper,.form-wrapper{
    width: 100%;
    height: 100%;
    background-color: #0000006c;
    z-index: 99;
    display: grid;
    place-items: center;
}
form{
    display: flex;
    flex-wrap: nowrap;
}
input{
    border: none;
    background: transparent;
    border-bottom: 1px solid var(--color5);
    margin-right: 10px;
    color: var(--color5);
    font-size: 1.2em;
}
input:focus{
    border:none;
}
input:target,input:target-within,input::target-text{
    border:none;
}
.addtask{
    font-size: 2em;
    font-family: 'Luckiest Guy';
    color: var(--color5);
    background: transparent;
    border:none;
    cursor: pointer;
    padding: 20px;
    line-height: 100%;
    transition: all 0.3s ease-in;
}
.addtask:hover{
    transform: scale(1.1);
    color: var(--color1);
}