.inprogress-container{
    display: grid;
    width: 100%;
    height: 100%;
}
.inprogress-title{
    grid-column: 1;
    grid-row: 1;
    position: relative;
    width: fit-content;
    font-family: sans-serif;
    font-size: 2em;
    padding-left: 20px;
    margin-left: 20px;
}
.inprogress-title-text{
    font-family: 'Luckiest Guy';
    position: relative;
    z-index: 2;
    color: var(--color5);
}
.inprogress-title::after{
    content: '';
    position: absolute;
    width: 80%;
    height: 50%;
    top: 25%;
    left: 0;
    background-color: var(--color1);
    z-index: 1;
    box-shadow: -10px 10px 30px 0px rgba(0, 0, 0, 0.212);
}
.inprogress-items-wrapper{
    position: relative;
    font-family: sans-serif;
    grid-column: 1;
    grid-row: 2 / span 10;
    overflow-x: auto;
}
.inprogress-items-wrapper::-webkit-scrollbar{
    display: none;
}
.inprogress-items-wrapper-2{
    font-size: 1.5em;
    position: absolute;
    display: flex;
    padding-left: 80px;
    padding-top: 10px;
    overflow-x: auto;
}
.inprogress-items-wrapper-2::-webkit-scrollbar{
    display: none;
}
.inprogress-item{
    max-width: 500px; 
    color: var(--color3);
    margin-right: 100px;
    display: flex;
}
.inprogress-item-no{
    margin-right: 5px;
}