:root{
    --color1: #4036AC;
    --color2: #5255CE;
    --color3: #B1D3F4;
    --color4: #FF9D9D;
    --color5: #FFFFFF;
}
*,*::after,*::before{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.app-container{
    overflow: hidden;
    width: 100vw;
    height: 100vh;
    background-color: var(--color2);
    display: grid;
    padding: 10px 30px;
}
.app-header{
    grid-column: 1/ span 5;
    grid-row: 1;
}
.app-dailytask{
    grid-column: 1 /span 2;
    grid-row: 5 / span 10;
}
.app-inprogress{
    grid-column: 1/ span 5;
    grid-row: 2 / span 3;
}
.app-projects{
    position: relative;
    grid-column: 3 / span 3;
    grid-row: 5 / span 10;
} 
.app-projects::before{
    content: '';
    position: absolute;
    width: 2px;
    height: 70%;
    left: -5%;
    top: 10%;
    background:var(--color5);
}
@media only screen and (max-width: 428px){
    .app-container{
        padding: 10px;
        grid-template-columns: repeat(1,1fr);
    }
    .app-inprogress{
        grid-column: 1;
        grid-row: 2 / span 2;
    }
    .app-dailytask{
        grid-column: 1;
        grid-row: 4 / span 5;
        margin-bottom: 10px;
    }
    .app-projects{
        grid-column: 1;
        grid-row: 9 / span 5;
    }
    .app-projects::before{
        display: none;
    }
    .dailyTask-container{
        width: 100%;
    }
    .inprogress-items-wrapper-2{
        padding: 10px;
        overflow-x: auto;
    }
}